
/*-------------------------------*/
/*           Wrappers            */
/*-------------------------------*/

#wrapper {
    min-height: 100vh;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    padding-left: 0;
    transition: all 0.5s ease;
    
    &.toggled {
        padding-left: @width1;
        #sidebar-wrapper {
            width: @width1;
        }
        #page-content-wrapper {
            margin-right: -@width1;
            height: 100vh;
            position: absolute;
        }
    }
    
    #page-wrapper {
        width: 100%;
    }
}

.row {
    div[class*='col-md-'].with-divider:after {
        content: "";
        display: block;
        position: absolute;
        right: 0;
        top: 0;
        width: 1px;
        height: 100%;
        background: #ededed;
    }
}
.cotrolbar {
    display: inline-block;
    width: 100%;
    min-height: 34px;
    vertical-align: bottom;
    span.note {
        line-height: 36px;
    }
}

#sidebar-wrapper {
    position:  fixed;
    top: 0;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    background: @bg-color;
    height: @full-height;
    left: @width1;
    margin-left: -@width1;
    overflow-x: hidden;
    overflow-y: auto;
    transition: all 0.5s ease;
    width: 0;
    height: 100%;
    z-index: 1000;
    &::-webkit-scrollbar {
        display: none;
    }
}

@media screen and ( min-width: 1367px ) {
    #sidebar-wrapper {
        width: @width1;
    }

    #page-content-wrapper {
        padding-left: @width1;
    }
}
#page-content-wrapper {
    width: @full-width;
    height: 100vh;
}

@media (min-width: 768px) {
    #page-content-wrapper {
        #page-wrapper {
            margin-left: 0;
        }
    }
}

.no-padding-left {
    padding-left: 0 !important;
}
.no-padding-right {
    padding-right: 0 !important;
}
