form.form-horizontal {
    
    label {
        font-weight: normal;
    }
    
    hr {
        margin-top: 0;
        margin-bottom: 20px;
    }

    label.photo-uploader {
        position: relative;
        display: block;
        width: 114px;
        height: 114px;
        overflow: hidden;
        border: 1px solid #dedfe0;

        input[type='file'] {
            position: absolute;
            opacity: 0;
        }
    }

    div[class*='col-'] {
        .form-group {
            display: inline-block;
            width: 100%;
            margin-bottom: 15px;
            
            div[class*="col-"] + span.percent {
                line-height: 36px;
            }
            .btn.edit {
                position: absolute;
                right: -25px;
                top: 0;
                font-size: 20px;
            }
            label { 
                line-height: 36px; 
                margin-bottom: 2px;
                &.compress {
                    line-height: 1.3em; 
                }
            }
            input { width: 100%; }
            input, 
            div.selection {
                height: 36px;
                line-height: 36px;
            }
            div.selection:after {
                top: 15px;
            }
        }
        
        div.thumbnail {
            padding: 0 0 100% 0;
            border-radius: 0;
            border: 0;
            box-shadow: 0px 0px 0px 1px rgba(0,0,0,.1);
        }

    }
    
    .carousel-list {
        display: inline-block;
        width: 100%;
        > * {
            position: relative;
            width: 107px;
            height: 62px;
            display: block;
            overflow: hidden;
            float: left;
            margin-right: 10px;
            border: 1px solid #e5e5e5;
        }

        > .add > input {
            position: absolute;
            left: 0; top: 0;
            opacity: 0;
        }
        > .add:before {
            width: 20px;
            height: 1px;
        }
        > .add:after {
            height: 20px;
            width: 1px;
        }
        > .add:before,
        > .add:after {
            content: "";
            position: absolute;
            display: block;
            left: 0; right: 0; top: 0; bottom: 0;
            margin: auto;
            background: #bcbcbc;
        }
    }

    div.indent {
        padding-left: 45px;
    }

    div.buttons > .btn {
        margin: 0 30px;
    }

    div.recommand-item {
        float: left;
        // margin-right: 30px;
        // width: 250px;
        height: 90px;
        
        .image {
            position: relative;
            float: left;
            width: 90px;
            height: 90px;
            display: block;
            border: 1px solid #e5e5e5;
            &:before {
                width: 20px;
                height: 1px;
            }
            &:after {
                height: 20px;
                width: 1px;
            }
            &:before,
            &:after {
                content: "";
                position: absolute;
                display: block;
                left: 0; right: 0; top: 0; bottom: 0;
                margin: auto;
                background: #bcbcbc;
            }
            input {
                position: absolute;
                left: 0; top: 0;
                opacity: 0;
            }
            img {
                position: absolute;
                width: 100%;
                left: 0; top: 0;
                top: 0; bottom: 0;
                margin: auto;
            }
        }
        .content {
            display: block;
            margin-left: 100px;
            div.selection {
                height: 28px;
                line-height: 28px;
                margin-bottom: 5px;
            }
        }
    }

    @media screen and (max-width: 640px) {
        div[class*='col-'] .form-group label {
            line-height: 1.2;
        }
    }
}

form.form-inline {

    display: inline-block;
    width: 100%;
        
    .flex {
        display: flex;
        flex-direction: row;
        padding-left: 5px;
        margin-left: -5px;

        > * {
            white-space: nowrap;
        }
        .toggler {
            margin-left: 5px;
            margin-right: 5px;
            .form-group {
                padding: 0;
                .form-control {
                    height: 36px;
                }
            }
        }
        div.selection.form-control {
            height: 36px;
        }
    }

    div.form-group {
        padding: 0 5px 0 0;
        line-height: 34px;
        
        &.pull-right {
            margin-right: 0;
        }
        
        input {
            width: 100% !important;
            .border-radius(1px);
        }

        &:last-child {
            padding-right: 0px;
        }
    }
    
    .btn {
        height: 34px;
    }
}

select.form-control,
select.form-control option,
input.form-control,
textarea.form-control {
    border: 1px solid #dedfe0;
    .border-radius(0);
    .inset-box-shadow();
    
    &[disabled],
    &.disabled {
        padding-left: 10px;
        padding-right: 10px;
        background: #f7f7f7;
        border-color: #eaeaea;
    }
    &:focus {
        .inset-box-shadow();
    }
}

div.checkbox {
    position: relative;
    display: inline-block;
    vertical-align: middle;

    input {
        position: absolute;
        display: none;
    }
    input + label {
        display: block;
        width: 20px;
        height: 20px;
        padding-left: 0;
        background: #FFF;
        border: 1px solid #b2b2b2;
        .border-radius(1px);
        font-family: 'icomoon' !important;
        speak: none;
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        text-transform: none;
        line-height: 1;

        /* Better Font Rendering =========== */
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;

        &:before {
            content: "\e908";
            display: none;
            width: 20px;
            height: 20px;
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            margin: auto;
            line-height: 1.5;
            text-align: center;       
        }
    }
    input:checked + label:before {
        display: block;
    }
}

div.selection.form-control {
    position: relative;
    display: block;
    overflow: hidden;
    border: 1px solid #dedfe0;
    background-color: #fff;
    padding: 0;
    .box-shadow();
    .border-radius(0);
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    
    &.inline {
        display: inline-block;
        height: 28px;
        line-height: 28px;
        width: auto;
        vertical-align: middle;
        ~ span {
            vertical-align: bottom;
            margin: 0 5px;
        }
        select {
            height: 28px;
            line-height: 28px;
            margin-right: 15px;
        }
    }

    &:after { 
        color: #111;
        content: "\e902";
        position: absolute;
        width: 9px;
        height: 9px;
        top: 0 !important;
        bottom: 0;
        font-size: 10px;
        line-height: 1;
        right: 10px;
        margin: auto;
        display: block;
        transform: scale(.7);
    }

    select {
        width: 125%;
        height: 100%;
        background-color: #fff;
        border: 0;
        outline: 0;
        
        &:focus {
            outline: 0;
        }
    }
}

.input { 
    &.short {
        width: 65px;
        &.very { width: 30px; }
    }
    &.wide {
        width: 65%; 
    }
    &.full-wide {
        width: 100%; 
    }
}
