ul.pagination {
    border: 0;

    li:first-child > a { color: #000; }
    li:last-child > a { color: #000; }
    li > a {
        font-size: 14px;
        border: 0;  
        color: rgba(0,0,0,.2);

        &:hover,
        &:focus {
            color: #000;
        }
    }

    li.active > a,
    li.active > a:hover,
    li.active > a:focus {
        color: rgba(0,0,0,1);
        border: 0;
        background: #FFF;
    }

}