#page-wrapper.coopration {
    display: flex;
    flex-direction: row;
    padding: 0;
    
    .maintain {
        flex: 1;
        background: #F9FAFA;
        min-height: calc(~"100vh - 45px");
        margin-right: 225px;

        div.row {
            margin-left: 0;
            margin-right: 0;
        }
        
        div.header,
        div.temp-group {
            background: #FFF;
        }

        div.header {
            font-size: 15px;
            border-bottom: 0;
        }

        div.temp-group {
            padding: 15px 0 15px 15px;
            border-bottom: 1px solid #EDEDED;
            
            div.group {
                display: inline-block;
                margin-right: 40px;
            }
            label, .inline.image {
                vertical-align: middle;
                margin: 0;
                margin-right: 15px;
                font-size: 14px;
            }
        }
        
        .tab-pane {
            a.project-add {
                color: #777777;
                margin-top: -50px;
                font-size: 24px;
                margin-right: -5px;
                text-decoration: none;
            }
        }
        
        .nav-pills {
            border-bottom: 0;
            li {
                float: none;
                display: inline-block;

                a { 
                    color: #C6C6C6; 
                    border: 0;
                    background: none;
                }

                a:hover, 
                a:focus, 
                &.active a {
                    color: #000;
                    background: transparent;
                }
            }
        }

        div.tab-content {
            padding: 15px 25px;
        }
    }
    
    div.header {
        font-weight: bold;
        height: 40px;
        line-height: 40px;
        padding: 0 15px;
        background: #FFF;
        border-bottom: 1px solid #EDEDED;
    }
}

#sticker {
    position: relative;
    padding-bottom: 35px;
    
    h5 {
        margin-top: 0;
        margin-bottom: 20px;
    }
    a.compelete {
        position: absolute;
        bottom: 0;
        right: 0;
    }

    div.sticker-group {
        position: relative;
        display: inline-block;
        width: 100%;
        margin-bottom: 16px;
        
        div.avatar {
            float: left;
            width: 26px;
            height: 26px;
        }
        div.sticker-list {
            margin-left: 50px !important;
            
            div[class*="col-"] {
                padding-left: 3px;
                padding-right: 3px;
            }

            .sticker {
                position: relative;
                height: 70px;
                padding: 8px;
                background: #FFF;
                border: 1px solid #EDEDED;
                margin-bottom: 3px;

                &.add:before,
                &.add:after {
                    content: "";
                    position: absolute; 
                    display: block;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    margin: auto;
                    background: #979797;
                }

                &.add:before {
                    width: 1px;
                    height: 21px;
                }
                &.add:after {
                    width: 21px;
                    height: 1px;
                }
            }
        }
    }
}