.row.panel-group {
    margin-left: 8px;
    margin-right: 8px;
    margin-bottom: 4px;

    > div[class*="col-"] {
        padding-left: 7px;
        padding-right: 7px;
        margin-bottom: 10px;

        .panel.panel-default {
            padding: 10px;
            border-color: #dedfe0;
        }
    }
}

.panel.panel-default {
    border-color: #eaeaea;
    .border-radius(0);
    .box-shadow();
    
    &.no-border {
        border: 0 !important;
        .panel-heading {
            border-width: 1px 1px 0 1px;
            border-style: solid;
            border-color: #dedfe0;
        }
    }

    .panel-heading {
        padding: 8px 15px;
        background: #f9fafa;
        border-color: #eaeaea;
        .border-radius();

        .nav.nav-pills {
            margin: 0;
            li a { 
                border: 0; 
                background: transparent;
            }
        }
    }
    
    .panel-title {
        font-size: 14px;
        line-height: 16px;
        margin-bottom: 10px;
        
        i[class*="icon"] {
            font-size: 16px;
            margin-right: 5px;
            line-height: 1;
            vertical-align: text-bottom;
        }
    }

    .panel-body {
        table.table {
            margin: 0;
        }
        
        &.no-padding {
            padding: 0;
            margin-bottom: 0;
        }
    }

    div.statics {
        .main {
            line-height: 1;
            font-size: 24px;
            margin-right: 5px;
        }
        .text-blue {
            color: #1a89c6;
            vertical-align: bottom;
        }
    }
}