
.navbar.navbar-default {
    min-height: 44px;
    height: 44px;
    margin-bottom: 0;
    border-left: 1px solid #e7e7e7;

    .navbar-brand {
        font-size: 24px;
        padding: 0;
        color: #000;
        margin-left: 30px;
        line-height: 50px;
        width: 103px;
        height: 44px;
        background-image: url(../images/logo.svg);
        background-repeat: no-repeat; 
        background-position: left center;
        background-size: auto 24px;
        text-indent: -9999px;
        overflow: hidden;
    }

    .navbar-toggle {
        position: fixed;
        top: 0; 
        left: 0;
        width: 33px; 
        height: 33px;
        padding: 8px 7px 7px;
        margin-top: 5px;
        margin-bottom: 6px;
        border-color: transparent;
        background-color: transparent;
        .border-radius(0);
        .transition(all, .5s);
        
        &:hover,
        &:focus {
            background-color: #fff;
            border-color: @side-active-bg;
        }

        .icon-bar {
            width: 16px;
            height: 1px;
            margin-bottom: 6px;
            background-color: black;
        }
        
        &.is-open {
            left: @width1;
        }
    }

    .navbar-right {
        li:last-child {
            padding-right: 0px !important;
        }
        li.avatar {
            position: relative;
            height: 44px;
            padding: 10px 15px;

            > a {
                position: relative;
                display: block;
                min-height: 0;
                padding: 0;
                width: 24px;
                height: 24px;
                overflow: hidden;
                .border-radius(50%);
                
                img {
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    width: 100%;
                    margin: auto;
                    .border-radius(50%);
                }
            }
        }
    }
}

/*-------------------------------*/
/*     Sidebar nav styles        */
/*-------------------------------*/

.sidebar-nav {
    list-style: none;
    margin: 0;
    padding: 0;
    position: absolute;
    width: @width1;

    li {
        position: relative;
        display: inline-block;
        position: relative;
        line-height: 36px;
        width: @full-width;
        
        &.main-title > .content {
            height: 44px;
            line-height: 44px;
        }
        
        > .content:hover,
        > .content:focus,
        > .content.active {
            background: @side-active-bg;
        }
        
        &:hover .extra,
        &:focus .extra {
            display: block;
        }

        &.notification > .content:before {
            content: "";
            position: absolute;
            display: block;
            width: 5px;
            height: 5px;
            top: 0; bottom: 0;
            margin: auto auto auto -15px;
            background: #1AAFD6;
            .border-radius(50%);
        }

        .extra {
            display: none;
        }

        ul {
            padding: 0;
            margin-left: -20px;
            margin-right: -15px;
            > li > .content {
                padding: 0 15px 0 85px;
            }
        }

        i[class*="icon"].pull-right {
            font-size: 16px;
        }

        i[class*="icon"] {
            float: left;
            width: 24px;
            height: 36px; 
            margin-right: 5px;
            font-size: 20px;
            line-height: 36px;
            display: block;
        }

        .content {
            // font-weight: 700;
            font-size: 14px;
            color: #111;
            padding-left: 20px;
            cursor: pointer;
            display: block;
            line-height: 36px;
            text-decoration: none;
        }
    }

    li.open {
        &:hover {
            before {
                transition: width .2s ease-in;
                width: @full-width;
            }
        }
    }
    .dropdown-menu {
        background-color: #222222;
        border-radius: 0;
        border: none;
        box-shadow: none;
        margin: 0;
        padding: 0;
        position: relative;
        width: @full-width;
    }
}

.nav.nav-pills {
    box-shadow: none;
}
.nav.nav-pills.text-center.relaxed.btn {
    width: 100%;
    margin-bottom: 25px;

    li {
        float: none;
        display: inline-block;
        vertical-align: top;
        padding: 0;
    
        a {
            color: #cccdce;
            border: 1px solid #cccdce;
            // height: 30px !important;
            padding: 4px 8px 4px 12px;
            line-height: 1.5;
            width: auto;
            height: auto;
            .border-radius();

            &:hover,
            &:focus {
                color: darken(#cccdce, 5%);
                border-color: darken(#cccdce, 5%);
                background-color: #FFF;
            }
        }

        &.active a {
            color: #008fce;
            background-color: #FFF;
            border: 1px solid #008fce;
        }
    }
}
.nav.nav-pills.index-table {
    li > a:hover,
    li > a:focus,
    li.active > a,
    li.active > a:hover,
    li.active > a:focus {
        color: #111;
    }
    li > a {
        background: #FFF;
        color: #c2c5c6;
    }
}

@media screen and ( max-width: 1366px ) {

    .navbar.navbar-default {

        .navbar-brand {
             margin-left: 50px;
        }
        
        .navbar-toggle {
            float: left;
            margin-right: 0px;
            margin-left: 15px;
            display: block;
        }
    }
}

@media screen and ( max-width: 768px ) {
    .navbar-header,
    .navbar-right.in  {
        display: inline-block;
    }
    .navbar-right.in {
        float: right;
    }
}
