table {
    &.table > tbody > tr > th,
    &.table > tbody > tr > td {
        padding: 12px 12px 11px;
        vertical-align: middle; 
        
        a > i[class*="icon-"] {
            font-size: 20px;
            line-height: 2;
            color: #777;
        }
    }
    > tbody > tr > th {
        font-weight: normal;
    }
    > thead > tr > th {
        position: relative;
    }
    &.table > tbody > tr > td {
        padding: 5px 8px;
        word-break: break-word;
        letter-spacing: 1px;
        
        &.text-muted,
        > a.text-muted:not(.label),
        > .text-muted:not(.label) {
            color: #e0e0e0 !important;
        }

        a:not(.label), a:link:not(.label) {
            color: #777;

            &:hover,
            &:focus {
                text-decoration: none;
            }

            &.text-primary,
            &.text-primary i[class*="icon-"],
            &.text-blue,
            &.text-blue i[class*="icon-"] {
                color: #1a89c6 !important;
            }
            &.text-success,
            &.text-success i[class*="icon-"] {
                color: #2db226 !important;
            }
            &.text-danger,
            &.text-danger i[class*="icon-"] {
                color: #ff0316 !important;
            }
        }

        a > i[class*=".icon-"] {
            line-height: 2;
        }
    }
    tr.search,
    tr.search th,
    tr.search td {
        height: 53px;
    }

    tr.bg-white,
    tr.bg-white th,
    tr.bg-white td,
    tr th.bg-white,
    tr td.bg-white {
        background-color: #FFF !important;
    }
    
    tr.search .horizontal-input {
        padding: 2px 7px !important;

        &.two-items {
            padding-left: 0px !important;
            padding-right: 0px !important;

            > input {
                display: inline-block;
                line-height: 1.7;
                font-size: 12px;
                padding: 5px;
                width: 39%;
                height: 32px;
                line-height: 32px;
            }   
        }

        input {
            height: 22px;
            line-height: 22px;
            margin-bottom: 2px;
            font-weight: normal;
            + input {
                margin-bottom: 0;
            }
        }
    }

    &.table > thead > tr > th {
        border-bottom-width: 1px;
        vertical-align: middle;
        font-weight: 400;
        input {
            line-height: 1.7;
            font-size: 12px;
            padding: 5px;
            height: 32px;
            line-height: 32px;
            &:focus {
                outline: 0;
                border-color: 1px solid @primary;
            }
        }
        .selection,
        .selection select {
            line-height: 1.7;
            option {
                font-size: 12px;
            }
        }
        .sort {
            position: absolute;
            top: 0;
            bottom: 0;
            right: 12px;
            height: 20px;
            margin: auto 0;
            &:before,
            &:after {
                content: "";
                display: block;
                margin: 2px 0;
                border-left: 4px solid transparent;
                border-right: 4px solid transparent;
            }
            &:before {
                border-bottom: 4px solid #CCC;
                border-top: 4px solid transparent;
            }
            &:after {
                border-top: 4px solid #CCC;
                border-bottom: 4px solid transparent;
            }
            &.asc {
                &:before {
                    opacity: 0;
                }
                &:after {
                    opacity: 1;
                    border-top-color: #111;
                }  
            }
            &.desc {
                &:after {
                    opacity: 0;
                }
                &:before {
                    opacity: 1;
                    border-bottom-color: #111;
                }  
            }
        }
    }
    
    // top right bottom left
    // top left-right bottom
    // top-bottom left-right

    // padding: 5px 10px 3px 8px;
    // padding: 5px 10px 8px;
    // padding: 5px 10px;
    // padding: 10px;
    
    // border-width: 5px 10px 3px;
    // border-style: solid;
    // border-color: #000;

    // border: 5px solid #000;

    &.table.relaxed > thead > tr > th {
        padding: 9px 15px;
        vertical-align: middle;
        line-height: 1;
        
    }
    &.table.relaxed > tbody > tr > td {
        padding: 5px 8px;
    }
}

.table-bordered {
    > thead > tr > th, 
    > tbody > tr > th, 
    > tfoot > tr > th, 
    > thead > tr > td, 
    > tbody > tr > td, 
    > tfoot > tr > td {
        border-color: #eaeaea;
        border-width: 1px;
    }
}

.table-bordered > thead > tr > th, .table-bordered > thead > tr > td
.table-striped > tbody > tr:nth-of-type(odd) {
    background-color: #f9fafa;
}