.modal {
    .modal-dialog {
        .close {
            font-size: 31px;
        }
        .modal-header {
            border-bottom: 0;
            padding: 25px 25px 0;
            h4 {
                margin: 0;
                font-size: 15px;
            }
        }
        .modal-body {
            padding: 0 25px 25px;

            div[id*="-chart"] {
                margin-top: 30px;
                margin-right: 120px;
            }
        }
    }
}