.avatar.image {
    position: relative;
    display: block;
    overflow: hidden;
    width: 30px; height: 30px;
    .border-radius(50%);

    img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
    }
}

.inline.image {
    display: inline-block !important;
}

.image.preview {
    display: block;
    margin-bottom: 5px;
}