    url('../fonts/icomoon.ttf?20cny3') format('truetype'),
    url('../fonts/icomoon.woff?20cny3') format('woff'),
    url('../fonts/icomoon.svg?20cny3#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
