.btn.disabled, 
.btn[disabled], 
fieldset[disabled] .btn {
    color: #FFF;
    background: #cecece;
}

.submit-buttons .btn {
    margin: 0 32px;
}

.btn {
    border: 0;
    height: 34px;
    line-height: 23px;
    font-weight: normal;
    letter-spacing: 2px;
    .border-radius(2px);

    + .note {
        margin-left: 15px;
        vertical-align: middle;

        &.status {
            margin-left: 0;
        }
        &.relaxed {
            padding-top: 10px;
            line-height: 2em;
        }
    }
    
    &.btn-small {
        height: 30px !important;
        padding: 4px 8px 4px 12px;
    }

    &.btn-thin {
        font-weight: thin;
    }
    
    &.btn-default {
        border: 1px solid #cccdce;
    }
    &.btn-primary {
        background: @primary;

        &:hover,
        &:focus {
            background: @primary-hover;
        }

        &.btn-inverse {
            background: #fff;
            border: 1px solid @primary;
            color: @primary;

            &:hover,
            &:focus {
                background: rgba(26, 137, 198, 0.1);
            }
        }

        &.btn-inverse:hover,
        &.btn-inverse:focus {
            border: 1px solid @primary-hover;
            color: @primary-hover;
        }
    }
}

.btn-uploader {
    position: relative;
    display: block;
    width: 100px;
    height: 30px;
    line-height: 30px;
    margin-bottom: 10px;
    color: @label-gray-text;
    border: 1px solid @label-gray-border;
    
    &:hover,
    &:focus {
        background: @bg-color;
    }
    span {
        display: block;
        width: 100%;
        height: 30px;
        line-height: 28px;
        text-align: center;
    }

    input {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
    }

    + .image > img {
        max-width: 100%;
    }
}

.uploader {
    position: relative;
    display: inline-block;
    overflow: hidden;
    border: 1px solid #cccccc;
    width: 70px;
    height: 20px;
    .border-radius(2px);
    
    + span {
        line-height: 20px;
        vertical-align: top;
    }

    span {
        display: block;
        text-align: center;
        line-height: 20px;
        font-size: 11px;
    }

    input {
        position: absolute;
        left: 0; top: 0;
        opacity: 0;
    }
}