a.label, 
.label {
	border-width: 1px;
	border-style: solid;
	border-color: #111;
	color: #111;
	font-weight: 400;
	text-align: center;
	display: inline-block;
	min-width: 23px;
	padding: 5px;
	.border-radius(2px);
	
	&:hover,
	&:focus {
		color: #111;
	}

	&.red {
		color: @label-red-text;
		border-color: @label-red-border;

		&:hover,
		&:focus {
			color: @label-red-text;
		}
	}


    &.gray {
		color: @label-gray-text;
		border-color: @label-gray-border;

		&:hover,
		&:focus {
			color: @label-gray-text;
		}
	}



    &.lightgray {
		color: @label-lightgray-text;
		border-color: @label-lightgray-border;

		&:hover,
		&:focus {
			color: @label-lightgray-text;
		}
	}



    &.green {
		color: @label-green-text;
		border-color: @label-green-border;

		&:hover,
		&:focus {
			color: @label-green-text;
		}
	}


	&.blue {
		color: @label-blue-text;
		border-color: @label-blue-border;

		&:hover,
		&:focus {
			color: @label-blue-text;
		}
	}
}
