.popover {
    width: 330px;
    border: 1px solid #EAEAEA;
    padding: 17px;
    .border-radius(2px);
    .box-shadow(0,0,0,0,rgba(0,0,0,0));

    .popover-title {
        background: transparent;
        padding-bottom: 10px;
    }
    .popover-content {
        padding-top: 10px;
    }
}

.popover.user-info {
    .popover-title {
        display: inline-block;
        width: 100%;
        padding-top: 0;
        .border-radius(4px);

        > * {
            font-weight: normal;
            vertical-align: middle;
        }
        .avatar {
            width: 36px; height: 36px;
            display: inline-block;
            overflow: hidden;
            margin-right: 10px;
            .border-radius(50%);

            img {
                width: 100%;
            }
        }

        span.gender {
            margin-left: 20px;
        }

        a.fb-link {
            float: right;
            width: 18px;
            height: 18px;
            display: block;
            overflow: hidden;
            margin-top: 8px;
            .border-radius(50%);

            > i {
                line-height: 1;
                color: #FFF;
                background: #000;
            }
        }
    }

}