.sort-container {
    margin-left: 0;
    margin-right: 0;
    
    &.carousel-list {
        display: block;
        height: 108px;
        > .thumbnail {
            float: left;
            display: block;
            width: 108px;
            padding: 0;
            margin: 0 10px 0 0;
            a.image {
                position: relative;
                display: block;
                line-height: 0;
                padding-bottom: 62.5%;
                margin: 0;
                border: 1px solid #ededed;

                img {
                    position: absolute;
                    left: 0; top: 0; bottom: 0; right: 0;
                    margin: 0;
                    border: 0;
                    width: auto;
                    height: 100%;
                    line-height: 0;
                }
            }
        }
        > .thumbnail-add {
            float: left;
            width: 108px;
            .add {
                position: relative;
                display: block;
                padding-bottom: 62.5%;
                border: 1px solid #ededed;
                
                &:hover,
                &:focus {
                    border-color: darken(#ededed, 5%);
                    &:after, 
                    &:before {
                        background: darken(#ededed, 5%);
                    }
                }
                &:after,
                &:before {
                    content: '';
                    position: absolute;
                    display: block;
                    left: 0; right: 0; top: 0; bottom: 0;
                    margin: auto;
                    background: #ededed;
                }
                &:after {
                    width: 22px;
                    height: 1px;
                }
                &:before {
                    height: 22px;
                    width: 1px;
                }
            }
        }
        .sort-placeholder { 
            height: 108px;
        }
    }

    .sort-placeholder {
        float: left;
        display: block;
        width: 90px;
        height: 110px;
        border: 1px dashed #ededed;
    }
    .thumbnail {
        border: 0;
        padding: 4px 12px;
        margin-bottom: 10px;
        .border-radius(0);

        .caption {
            padding-left: 0;
            padding-right: 0;
        }
        .caption h5 {
            margin: 0;
            text-align: center;
        }
    }
}