
.background-retina(@image, @image2, @width, @height) {
    background-image: url(@image);
    background-size: @width @height;
    
    @media (min--moz-device-pixel-ratio: 1.3),
         (-o-min-device-pixel-ratio: 2.6/2),
         (-webkit-min-device-pixel-ratio: 1.3),
         (min-device-pixel-ratio: 1.3),
         (min-resolution: 1.3dppx) {
    /* on retina, use image that's scaled by 2 */
        background-image: url(@image);
    }
}

.border-radius(@r: 0) {
    -webkit-border-radius: @r;
    -moz-border-radius: @r;
    -ms-border-radius: @r;
    -o-border-radius: @r;
    border-radius: @r;
}

.transition(@type: all,  @sec: .5s) {
    -webkit-transition: @type @sec;
    -moz-transition: @type @sec;
    -ms-transition: @type @sec;
    -o-transition: @type @sec;
    transition: @type @sec;
}

.box-shadow(@x:0, @y:0, @blur:0, @spread:0, @rgba:rgba(0,0,0,0)) {
    -webkit-box-shadow: @x @y @blur @spread @rgba;
    -moz-box-shadow: @x @y @blur @spread @rgba;
    -ms-box-shadow: @x @y @blur @spread @rgba;
    -o-box-shadow: @x @y @blur @spread @rgba;
    box-shadow: @x @y @blur @spread @rgba;
}

.inset-box-shadow(@x:0, @y:0, @blur:0, @spread:0, @rgba:rgba(0,0,0,0)) {
    -webkit-box-shadow: inset @x @y @blur @spread @rgba;
    -moz-box-shadow: inset @x @y @blur @spread @rgba;
    -ms-box-shadow: inset @x @y @blur @spread @rgba;
    -o-box-shadow: inset @x @y @blur @spread @rgba;
    box-shadow: inset @x @y @blur @spread @rgba;
}