.datepicker {
    padding: 10px 20px;
    border: 1px solid #ddd;
    border-radius: 0px !important;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, .2);
    
    &:before,
    &:after {
        display: none;
    }

    .table-condensed > thead > tr > th.prev,
    .table-condensed > thead > tr > th.next {
        font-family: 'icomoon' !important;
        speak: none;
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #FFF;

        &:before {
            position: absolute;
            left: 0; right: 0;
            margin: auto;
            color: #111;
        }
    } 

    .table-condensed > thead > tr > th.prev:before {
        content: "\e90a";
    }
    .table-condensed > thead > tr > th.next:before {
        content: "\e90b";
    }

    .table-condensed > thead > tr > th.dow {
        color: #c4c6c7;
        font-weight: 300;
    }
    .table-condensed > tbody > tr > td.old.day,
    .table-condensed > tbody > tr > td.new.day {
        color: #c4c6c7;
        font-weight: 300;
    }
    .table-condensed > tbody > tr > td {
        border-radius: 50%;
        margin: 0 3px;
        letter-spacing: 0px;
    }
    table tr td.active:active, 
    table tr td.active.highlighted:active, 
    table tr td.active.active, 
    table tr td.active.highlighted.active {
        background: @primary;
        position: relative;
    }
}