.nav.nav-pills {
    margin-top: 20px;
    li {
        margin-right: 5px;
        a {
            width: 75px;
            height: 28px;
            line-height: 28px;
            font-size: 13px;
            color: #CCCDCE;
            padding: 0;
            text-align: center;
            border: 1px solid #CCCDCE;
            background: #FFF;
            cursor: pointer;
            .border-radius(1px);
        }
        &.active a,
        &.active a:hover,
        &.active a:focus,
        a:hover,
        a:focus {
            color: #1a89c6;
            border-color: #1a89c6;
            background: #FFF;
        }
    }
}

.tab-content .circle-label {
    display: inline-block;
    width: 12px;
    height: 12px;
    margin-right: 10px;
    vertical-align: middle;
    .border-radius(50%);

    &.red {
        border: 1px solid rgba(242, 20, 67, .6);
    }
    &.green {
        border: 1px solid rgba(12, 170, 16, .6);
    }
}

&.index-table {
    > .tab-pane {
        display: block;
    }
}


@media screen and (max-width: 768px) {
    .tab-content.index-table {
        > .tab-pane:not(.active) {
            display: none;
        }
    }
}
