#page-wrapper.coopration .maintain div.tab-content.gantter {
    padding-top: 0;
}

#gantter {
    padding-top: 0;
    padding-bottom: 0;
    
    div.gantter-container {
        position: relative;
        display: inline-block;
        width: 100%;
        min-height: 315px;
        background: #FFF;
        border: 1px solid #EDEDED;

        div.project-event {
            float: left;
            left: 0;
            width: 94px;
            padding-top: 45px;
            
            div.event {
                position: relative;
                border-top: 1px solid #EDEDED;
                display: table;
                width: 100%;
                padding-left: 12px;
                line-height: 29px;
                min-height: 30px;
                
                &.have-sub span {
                    position: absolute;
                    top: 0; bottom: 0;
                    margin: auto;
                    height: 2em;
                }
                .sub {
                    float: right;
                    width: 46px;
                    border-left: 1px solid #EDEDED;
                }
                .sub .event {
                    float: left;
                    padding-left: 0;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    text-align: center;
                    font-size: 0.5em;
                    letter-spacing: 0;

                    &:first-child {
                        border-top: 0;
                    }
                }
            }
        }

        div.schedule_container {
            position: absolute;
            display: flex;
            flex-direction: row;
            height: 100%;
            width: 100%;
            left: 0;
            top: 0;
            overflow-x: auto;
            overflow-y: hidden;
            white-space: nowrap;

            .schedule {
                position: absolute;
                display: block;
                height: 20px;
                line-height: 20px;
                text-align: center;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                font-size: 12px;
                font-weight: 300;
                color: #FFF;
                border-left: 1px solid #FFF;
                border-right: 1px solid #FFF;
                background: rgba(42,170,234,.3);
                opacity: .75;
                cursor: pointer;
                
                &:hover,
                &:focus {
                    opacity: 1;
                }

                &.old {
                    background: rgba(196,198,199,.25);
                    color: #999;
                }

                &.active {
                    background: rgba(42,170,234,1);
                }
            }
        }

        div.timeline {
            position: absolute;
            display: flex;
            flex-direction: row;
            height: 100%;
            width: calc(~'100% - 94px');
            border-left: 1px solid #EDEDED;
            margin-left: 94px;
            overflow-x: auto;
            overflow-y: hidden;
            white-space: nowrap;
            
            .month {
                position: relative;
                float: left;
                height: 100%;
                display: table;
                
                .month_header {
                    display: inline-block;
                    font-size: 12px;
                    text-align: center;
                    border-right: 1px solid #EDEDED;
                    border-bottom: 1px solid #EDEDED;
                    
                    .month_title,
                    .week_container > .week {
                        line-height: 22px;
                        color: #BBBBBB;
                    }

                    .month_title {
                        border-bottom: 1px solid #EDEDED;
                    }
                    .week_container > .week {
                        float: left;
                        width: 77px;
                        &:not(:last-child) {
                            border-right: 1px solid #EDEDED;
                        }
                    }
                }

                .month_body {
                    position: absolute;
                    width: 100%;
                    height: calc(~'100% - 45px');
                    display: block;

                    .week {
                        float: left;
                        width: 77px;
                        height: 100%;
                        background-color: #fff; 
                        background-image: linear-gradient(#EDEDED 1px, transparent 1px);
                        background-size: 100% 30px;
                    }
                }
            }

            > .line {
                position: absolute;
                width: 1px;
                top: 46px;
                height: calc(~'100% - 46px');
                background: #136B98;
                opacity: .7;
            }
        }
    }
}
