body {
    position: relative;
    overflow-x: hidden;
}
body,
html {
    overflow-y: auto;
    overflow-x: hidden;
    height: @full-height;
    background-color: @bg-color;
    letter-spacing: 2px;
    font-weight: 400;
    font-family: "Helvetica Neue", Helvetica, Arial, "PingFang TC", '微軟正黑體', sans-serif;
}

.nav {
    .open>a {
        background-color: transparent;
        &:hover {
            background-color: transparent;
        }
        &:focus {
            background-color: transparent;
        }
    }
}
a, a:link {
    text-decoration: none;
    &:hover,
    &:focus {
        text-decoration: none;
    }
}

a.text-black {
    cursor: pointer;
}
a.text-muted,
.text-muted {
    color: #878787;
}
.text-black,
a.text-black {
    color: #111 !important;
}

a.text-danger,
.text-danger {
    color: @text-alert;
}
span.v-bottom {
    vertical-align: bottom;
}

.clearfix {
    margin-bottom: 10px;
}

.xs-show {
    display: none !important;
}

.lg-hide {
    display: block;
}

th.lg-hide,
td.lg-hide {
    display: table-cell;
}

@media screen and (max-width: 1280px) {
    th.lg-hide, 
    td.lg-hide, 
    .lg-hide {
        display: none;
    }
}

@media screen and (max-width: 768px) {

    .xs-show {
        display: block !important;
    }

    
}